export let ParentFeature = {
    cursor: 'default',
    bubble: true,
    strokeColor: '#fff', //线颜色
    fillColor: null,
    strokeWeight: 2, //线宽
}
// --------- 动态获取节点颜色 ------------
// 输入描边颜色与填充色
export function getFeature(StrokeColor, FillColor) {
    return {
        cursor: 'default',
        bubble: true,
        strokeColor: StrokeColor, //线颜色
        strokeOpacity: 1, //线透明度
        strokeWeight: 1, //线宽
        fillColor: FillColor, //填充色
        fillOpacity: 0.35, //填充透明度
    }
}